import React from "react";

export default class ProductFilterView extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.props.getMatchToData();
    }

    componentDidUpdate(prevProps) {
        if (document.getElementById("opl-match-to-filter-expander-main" + this.props.isModal) &&
            !document.getElementById("opl-match-to-filter-expander-main" + this.props.isModal).getAttribute("data-js-processed")) {

            OPL.UI.initModules(document.getElementById("matchToFilter"));
            OPL.UI.on("modules.categorytree.clickevent", event => {
                this.bindEvent(event)
            }, "match-to-tree");
            OPL.UI.on("modules.categorytree.clearevent", event => {
                this.clearAll(event)
            }, "match-to-tree");
        }
        let initModulesNeeded = false;
        if (this.props.filters != null) {
            this.props.filters.map(element => {
                let attributeId = element.name.replace(/\s+/g, "-").toLowerCase();
                if (this.componentNeedInitialization("opl-product-filter-attribute-main-" + attributeId + this.props.isModal)) {
                    initModulesNeeded = true;
                }
            });
        }
        if(this.componentNeedInitialization("opl-match-to-filter-expander-main" + this.props.isModal)
            || this.componentNeedInitialization("opl-product-filter-expander-main" + this.props.isModal)){
            initModulesNeeded = true;

        }
        if (initModulesNeeded && this.props.isModal) {
            OPL.UI.initModules(document.getElementById("ora-product-list-section-modal-product"));
        } else if (initModulesNeeded){
            OPL.UI.initModules(document.getElementById("ora-product-filter-component"));
        }
    }
    componentNeedInitialization(name){
        return document.getElementById(name) != null
            && !document.getElementById(name).hasAttribute("data-js-processed")
    }

    getStateOfRadio(name,value){
        return this.props.selectedFilters[name] === value;
    }

    getStateOfCheckboxes(name,value){
        var values = this.props.selectedFilters[name];
        if(values){
            return (values.indexOf(value) != -1);
        }
        return false;
    }

    getStateOfNumberInput(element, type){
        var value = this.props.selectedNumberFilters[element.code+"_"+type];
        return value ? value : "";
    }

    renderValueCheckBox(element, parent, index){
        return (<ul key={parent.code+"_"+index} className="u-spacing-l">
            <li className="u-spacing">
                <label className="o-checkbox opl-checkbox">
                    <input type="checkbox"
                           name={this.props.component + "_" + parent.code}
                           onClick={(e,checked) => this.changeAttribute(element, parent, true, "add", checked)}
                           checked={this.getStateOfCheckboxes(parent.code,element)} />
                    <span className="o-ci"/>
                    <span className="o-ci-label u-font-normal">{element}
                        <span className="g-gray4-c u-font-small"/>
                            </span>
                </label>
            </li>
        </ul>);
    }

    changeAttribute(element, parent, multiValue, operation, e){
        this.props.changeFilterAttribute(element, parent.code, multiValue, operation);
        this.props.reloadTree();
    }

    changeNumberAttribute(type, value, element){
        this.props.changeFilterNumberAttribute(type, value, element);
        this.props.reloadTree();
    }

    changeNumberAttributeProps(type,value,element){
        this.props.changeFilterNumberAttribute(type, value, element);
    }

    renderValueRadio(element, parent, index){
        return (<ul key={parent.code+"_"+index} className="u-spacing-l">
            <li className="u-spacing">
                <label className="o-radio opl-radio">
                    <input type="radio"
                           name={this.props.component + "_" + parent.code}
                           onClick={(e,checked) => this.changeAttribute(element, parent, false, "add", checked)}
                           checked={this.getStateOfRadio(parent.code,element)} />
                    <span className="o-ci"/><span className="o-ci-label u-font-normal">{element}<span className="g-gray4-c u-font-small"/></span>
                </label>
            </li>
        </ul>);
    }

    renderInputString(element){
        return (<div className="opl-input">
                    <input type="text"/>
                </div>);
    }

    renderNumberPair(element){
        return(
            <div className="l-row u-spacing-l">
                {this.renderInputNumber(element, "from")}
                {this.renderInputNumber(element, "to")}
            </div>
        )
    }

    renderInputNumber(element, type){
        return (
            <div className="l-col l-col-6 ">
                <input
                    type="text"
                    placeholder={type=="from"?"Od":"Do"}
                    value={this.getStateOfNumberInput(element, type)}
                    className="opl-input--size-m"
                    onBlur={event => {this.changeNumberAttribute(type, event.target.value, element)}}
                    onKeyPress={event => {if (event.key === "Enter") {this.changeNumberAttribute(type,event.target.value,element)}}}
                    onChange={event => {this.changeNumberAttributeProps(type,event.target.value, element)}}
                />
            </div>
        )
    }

    renderAttribute(element) {
        let attributeId = element.name.replace(/\s+/g, "-").toLowerCase();
        return (
            <div id={"opl-product-filter-attribute-expanders-" + attributeId + this.props.isModal} className="single-filter">
                <div id={this.props.id || "opl-product-filter-attribute-main-" + attributeId + this.props.isModal}
                     data-js-module="common/modules/opl-expander"
                     data-js-options={JSON.stringify({
                         scrollToSelected: false, triggerSelector: ".opl-section__trigger",
                         "contentSelector": ".opl-section__content", "parentClass": "opl-section",
                     })}
                     role="tablist" aria-multiselectable="false">

                    <div className="opl-section u-no-padding">
                        <div className="opl-section__header u-no-padding">
                            <div className="o-separator u-spacing-l u-spacing-top-l"/>
                            <div className="l-row">
                                <div className="l-col l-col-9 ">
                                    <p className="h4 u-spacing-l">{element.name}</p>
                                </div>
                                <div className="l-col l-col-3 ">
                                    <a href="#"
                                       className="u-text-right u-block opl-section__trigger opl-wiking-expander__trigger--plus"
                                       role="tab">
                                        <span className="g-icon g-icon--only g-icon--plus g-icon--xs"/>
                                        <span className="u-acc-hide">Rozwiń sekcję</span>
                                    </a>
                                </div>
                            </div>
                        </div>

                        <div className="opl-section__content u-hide--soft"
                             role="tabpanel">
                            {element.type == "enum" ? element.availableValues.map((pos, index) => {

                                return element.multiValue ? this.renderValueCheckBox(pos, element, index) : this.renderValueRadio(pos, element, index);
                            }) : ""}
                            {element.type == "string" && this.renderInputString(element)
                            }
                            {element.type == "number" && this.renderNumberPair(element)
                            }
                            <div className="u-clear-both"/>
                        </div>
                    </div>
                </div>
            </div>);
    }

    handlePriceFilter(type,price){
        this.props.changePriceFilter(type, price);
        this.props.reloadTree();
    }

    handlePricePropsFilter(type,price){
        this.props.changePriceFilter(type, price);
    }

    renderPriceFilter() {
        return this.props.isSalesOfGoodsPage ? (<div id={"opl-product-filter-expanders" + this.props.isModal}>
                <div className="u-clear-both"/>
                <div id={"opl-product-filter-expander-main" + this.props.isModal} data-js-module="common/modules/opl-expander"
                     data-js-options={JSON.stringify({
                         scrollToSelected: false, triggerSelector: ".opl-section__trigger",
                         "contentSelector": ".opl-section__content", "parentClass": "opl-section",
                     })}
                     role="tablist" aria-multiselectable="false">

                    <div className={"opl-section u-no-padding" + (this.props.initiallyExpanded ? " is-expanded" : "")}
                         aria-expanded={(this.props.initiallyExpanded ? "true" : "false")}>
                        <div className="opl-section__header u-no-padding">
                            <div className="o-separator u-spacing-l u-spacing-top-l"/>
                            <div className="l-row">
                                <div className="l-col l-col-9 ">
                                    <p className="h4 u-spacing-l">{this.props.title}
                                        <span className="g-gray4-c u-font-small"> (zł)</span>
                                    </p>
                                </div>
                                <div className="l-col l-col-3 ">
                                    <a href="#"
                                       className="u-text-right u-block opl-section__trigger opl-wiking-expander__trigger--plus"
                                       role="tab">
                                        <span className="g-icon g-icon--only g-icon--plus g-icon--xs"/>
                                        <span className="u-acc-hide">{"Rozwiń sekcję " + this.props.title}</span>
                                    </a>
                                </div>
                            </div>
                        </div>

                        <div className="opl-section__content u-hide--soft"
                             role="tabpanel">
                            <div className="l-row u-spacing-l">
                                <div className="l-col l-col-6 ">
                                    <input type="text" placeholder="Od" className="opl-input--size-m"
                                           onBlur={event => {
                                               this.handlePriceFilter("from", event.target.value)
                                           }}
                                           onKeyPress={event => {
                                               if (event.key === "Enter") {
                                                   this.handlePriceFilter("from", event.target.value)
                                               }
                                           }}
                                           onChange={event => {
                                               this.handlePricePropsFilter("from", event.target.value)
                                           }}
                                           value={this.props.selectedPrice.from}
                                    />
                                </div>
                                <div className="l-col l-col-6 ">
                                    <input type="text" placeholder="Do" className="opl-input--size-m"
                                           onBlur={event => {
                                               this.handlePriceFilter("to", event.target.value)
                                           }}
                                           onKeyPress={event => {
                                               if (event.key === "Enter") {
                                                   this.handlePriceFilter("to", event.target.value)
                                               }
                                           }}
                                           ref={ref => this.priceTo = ref}
                                           onChange={event => {
                                               this.handlePricePropsFilter("to", event.target.value)
                                           }}
                                           value={this.props.selectedPrice.to}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        ) : null;
    }
    handleModelChooseAction(modelId, checked){
        this.props.setSelectedModel(modelId, checked);
        this.props.reloadTree();
    }

    handleProducerChooseAction(producerId, checked){
        this.props.setSelectedRecomendedProducer(producerId, checked);
        this.props.reloadTree();
    }
    bindEvent(event){
        if(event.inputName=="model"){
            this.handleModelChooseAction(event.liDataId, event.checked);
        }
        if(event.inputName=="producent"){
            this.handleProducerChooseAction(event.liDataId, event.checked);
        }
    }
    clearAll(event){
        this.props.clearMatchToFilters();
    }

    renderMatchToFilter() {
        if (this.props.products.mainCategory == "accesories") {
            return (
                <div className="l-row u-small-hide u-medium-hide" id={"matchToFilter"}>
                    <div id={this.props.id || "opl-match-to-filter-expander-main" + this.props.isModal}
                         data-js-module="common/modules/opl-expander"
                         data-js-options={JSON.stringify({
                             scrollToSelected: false, triggerSelector: ".opl-section__trigger",
                             "contentSelector": ".opl-section__content", "parentClass": "opl-section",
                         })}
                         role="tablist" aria-multiselectable="false">
                        <div className="opl-section u-no-padding">
                            <div className="opl-section__header u-no-padding">
                                <div className="o-separator u-spacing-l u-spacing-top-l"/>
                                <div className="l-row">
                                    <div className="l-col l-col-9 ">
                                        <p className="h4 u-spacing-l">Pasujące do</p>
                                    </div>
                                    <div className="l-col l-col-3 ">
                                        <a href="#"
                                           className="u-text-right u-block opl-section__trigger opl-wiking-expander__trigger--plus"
                                           role="tab">
                                            <span className="g-icon g-icon--only g-icon--plus g-icon--xs"/>
                                            <span className="u-acc-hide">Rozwiń sekcję</span>
                                        </a>
                                    </div>
                                </div>
                            </div>
                            <div className="opl-section__content u-hide--soft"
                                 role="tabpanel">
                                <div data-js-module="common/modules/opl-category-tree" id={"match-to-tree"}
                                     data-js-options={JSON.stringify(this.props.matchToData)}
                                     className="opl-category-tree">
                                    <div className="l-row u-small-hide u-medium-hide">
                                        <div className="l-col l-col-12  u-text-right">
                                            <a href="#" className="opl-category-tree__clear">Wyczyść</a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>)
        } else {
            return <div/>;
        }
    }
    render() {
        return this.props.filters ? (<div>
            {this.renderPriceFilter()}
            {this.renderMatchToFilter()}
            {this.props.filters.map((pos, index) => {return this.renderAttribute(pos)})}
        </div>) : null;

    }
}
